/********************
      BUTTONS
********************/
button {
  border: none;
  cursor: pointer; }

.btn {
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 5px;
  padding: 1.4rem 4.2rem;
  position: relative;
  text-transform: uppercase;
  transition: all 600ms cubic-bezier(0.77, 0, 0.175, 1);
  user-select: none; }
  .btn:before, .btn:after {
    content: '';
    position: absolute;
    transition: inherit;
    z-index: 0; }
  .btn:hover {
    color: #f36424;
    transition-delay: .6s; }
    .btn:hover:before {
      transition-delay: 0s; }
    .btn:hover:after {
      background: #fff;
      transition-delay: .4s; }
  .btn span {
    position: relative;
    z-index: 1; }

.from-bottom {
  margin-bottom: 50px; }
  .from-bottom:before, .from-bottom:after {
    height: 0;
    left: 0;
    width: 100%; }
  .from-bottom:before {
    border: 1px solid #fff;
    border-bottom: 0;
    border-top: 0;
    top: 0; }
  .from-bottom:after {
    bottom: 0;
    height: 0;
    left: 1px; }
  .from-bottom:hover:before, .from-bottom:hover:after {
    height: 100%; }

.btn-submit,
.btn-cancel {
  padding-right: 80px;
  padding-left: 80px;
  font-size: 20px;
  line-height: 44px;
  background: #f36424;
  color: #fff;
  cursor: pointer;
  float: left;
  font-weight: 600;
  height: 45px;
  text-align: center; }

.btn-cancel {
  background: #fff;
  border: 1px solid #f36424;
  color: #f36424; }

.btn--small {
  padding-right: 40px;
  padding-left: 40px;
  min-width: 180px; }

/********************
        FORM
********************/
.data-form {
  margin-right: auto;
  margin-left: auto;
  max-width: 630px; }

.form-item {
  border-bottom: 2px solid #d9d9d9;
  margin-bottom: 40px;
  position: relative;
  width: 100%; }
  .form-item input {
    background: transparent;
    border: none;
    color: #555;
    display: block;
    font-size: 18px;
    height: 52px;
    line-height: 1.2;
    padding: 0 5px;
    width: 100%; }
    .form-item input.has-val + .focus-input:after {
      font-size: 15px;
      top: -20px; }
    .form-item input.has-val + .focus-input:before {
      width: 100%; }
    .form-item input:focus + .focus-input:before {
      width: 100%; }
    .form-item input:focus + .focus-input:after {
      font-size: 15px;
      top: -20px; }

.form-action {
  display: flex;
  justify-content: space-around;
  width: 100%; }

.focus-input {
  display: block;
  height: 100%;
  left: 0;
  pointer-events: none;
  position: absolute;
  top: 0;
  width: 100%; }
  .focus-input:before {
    background: #f36424;
    bottom: -2px;
    content: "";
    display: block;
    height: 2px;
    left: 0;
    position: absolute;
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    -moz-transition: all .4s;
    transition: all .4s;
    width: 0; }
  .focus-input:after {
    color: #999;
    content: attr(data-placeholder);
    display: block;
    font-size: 18px;
    left: 0;
    line-height: 1.2;
    padding-left: 5px;
    position: absolute;
    top: 15px;
    transition: all .4s;
    width: 100%; }

.validate-input {
  position: relative; }

.alert-validate:before {
  background-color: #fff;
  border: 1px solid #c80000;
  border-radius: 2px;
  color: #c80000;
  content: attr(data-validate);
  font-size: 13px;
  line-height: 1.4;
  max-width: 70%;
  opacity: 0;
  padding: 4px 25px 4px 10px;
  pointer-events: none;
  position: absolute;
  right: 0;
  text-align: left;
  top: 50%;
  transform: translateY(-50%);
  transition: opacity .4s;
  visibility: hidden; }
  @media only screen and (max-width: 1199px) {
    .alert-validate:before {
      opacity: 1;
      visibility: visible; } }

.alert-validate:after {
  background-color: #fff;
  color: #c80000;
  content: "\f06a";
  display: block;
  font-family: "FontAwesome";
  font-size: 16px;
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%); }

.alert-validate:hover:before {
  opacity: 1;
  visibility: visible; }

